@import "sass/global_styles/bootstrap-classes";
@import "sass/global_styles/global";
@import "sass/global_styles/header";
@import "sass/global_styles/footer";

.pp-banner {
  text-align: center;
  position: relative;
  z-index: 1;

  h1 {
    font-size: 64px;
    text-align: center;
    color: #0e0e11;
    letter-spacing: -1.28px;
    margin: 100px 0 32px;

    @media #{$tablet} {
      font-size: 40px;
      margin: 40px 0 20px;
    }
    @media #{$mobile} {
      font-size: 28px;
      margin: 40px 0 20px;
    }
  }
}

.pp-content {
  .pp-text {
    background: #fff;
    border-radius: 40px;
    padding: 60px;
    @media #{$tablet} {
      padding: 40px;
      border-radius: 30px;
    }
    @media #{$mobile} {
      padding: 24px 20px;
      border-radius: 24px;
    }
  }
  .topic {
    margin-bottom: 40px;
    @media #{$maxTab} {
      margin-bottom: 30px;
    }
    h2 {
      font-size: 28px;
      color: #0e0e11;
      margin-bottom: 20px;
      @media #{$maxTab} {
        font-size: 22px;
        margin-bottom: 12px;
      }
    }

    h3 {
      font-size: 20px;
      color: #0e0e11;
      margin-bottom: 15px;

      @media #{$mobile} {
        font-size: 16px;
        margin-bottom: 10px;
      }
    }

    p {
      font-size: 20px;
      @media #{$maxTab} {
        font-size: 16px;
      }

      &:not(:last-of-type) {
        margin-bottom: 15px;
        @media #{$maxTab} {
          margin-bottom: 12px;
        }
      }
      a {
        font-weight: 500;
        color: #5856d6;
        transition: 0.2s;
        &:hover {
          text-decoration: underline;
          color: #4f4ec0;
        }
      }
    }
    ul {
      margin-bottom: 15px;
      padding-left: 30px;
      @media #{$maxTab} {
        padding-left: 20px;
        margin-bottom: 10px;
      }
      li {
        font-size: 20px;
        list-style: square;
        @media #{$maxTab} {
          font-size: 16px;
        }

        &::marker {
          font-size: 12px;
        }
      }
    }
  }
}
